/* CSS Mini Reset */
html, body, div, form, fieldset, legend, label {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
th, td {
  text-align: left;
  vertical-align: top;
}
h1, h2, h3, h4, h5, h6, th, td, caption {
  font-weight: normal;
}
img {
  border: 0;
}
*, ::before, ::after {
  box-sizing: border-box;
}
html, body {
  height: 100%;
}
body {
  background: #E0E4CC;
}
h1, h2, h3, h4 {
  margin-top: 0;
}
.header {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
:root .title, .tagline {
  margin: 1em 2em;
}
.title {
  font-size: 1.2em;
}
.tagline {
  font-size: 1em;
}



